@import "main";
// End Of Import

// Preloader
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-prim;
  @include Flex;
  z-index: 9999;

  .spinner {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px solid $color-prim;
    border-left: 4px solid $color-white;
    animation: spin 1s linear infinite;
  }
}

// Mob Nav
.menuTriger {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  //   background-color: rgba($color-prim, 0.8);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 99;
  display: none;

  @include Sm {
    display: flex;
  }

  i {
    color: $color-prim;
    font-size: 29px;
  }
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color-sec, 0.95);
  z-index: 9;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: $color-white;
  z-index: 99;
  overflow-y: auto;
  transition: 0.5s;
  @include Flex;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);

  html[dir="ltr"] & {
    transform-origin: left;
  }

  &.open {
    // transform-origin: left;
    transform: scaleX(1);

    ul {
      opacity: 1;

      li {
        transform: translateX(0);
        opacity: 1;
      }
    }

    .contact{
      a{
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #fff;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background-color: $color-prim;
      font-family: "itBold";
    }

    .search_pop {
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: $color-prim;
      color: #FFF;
      font-size: 14px;
      margin-left: auto;
      margin-right: 20px;

      html[dir="ltr"] & {
        margin-left: 20px;
        margin-right: auto;
      }
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: $color-prim;
      cursor: pointer;
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 10px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.3s;
      }
      &:nth-child(2) {
        transition-delay: 0.5s;
      }
      &:nth-child(3) {
        transition-delay: 0.7s;
      }
      &:nth-child(4) {
        transition-delay: 0.9s;
      }

      a {
        width: 100%;
        display: block;
        color: $color-prim;
        transition: 0.5s;
        font-size: 17px;
        font-family: "itBold";
      }

      i {
        position: absolute;
        top: 13px;
        left: 13px;
        transform: rotate(90deg);
        font-size: 9px;
        color: currentColor;
        transition: 0.5s;

        html[dir="ltr"] & {
          left: unset;
          right: 13px;
        }

        &.open {
          transform: rotate(0deg);
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }

  .contact {
    text-align: center;
    a {
      color: $color-prim;
      display: block;
      margin-top: 15px;
      font-size: 16px;
      font-family: "itMd";
      transition: .5s;
      opacity: 0;
      transform: translateX(10px);
      
      &:nth-child(1) {
        transition-delay: 1.1s;
      }
      &:nth-child(2) {
        transition-delay: 1.3s;
      }
    }

    i {
      margin-left: 13px;

      html[dir="ltr"] & {
        margin-left: 0;
        margin-right: 13px;

        &.fa-phone{
          transform: rotateY(180deg);
        }
      }
    }
  }

  .social {
    margin-top: 20px;

    a {
      margin-left: 20px;
      font-size: 16px;

      &:last-child {
        margin-left: 0;
      }

      html[dir="ltr"] & {
        margin-left: 0;
        margin-right: 20px;
      }

      i{
        color: $color-prim;
      }
    }
  }
}

.head{
  position: fixed;
  top: 0;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  background-color: #fff;
  z-index: 9;
}

// Start Up
.up {
  padding-top: 30px;
  background-color: #fff;

  @include Sm {
    display: none;
  }

  .content {
    display: flex;
    color: #128c8d;
    direction: rtl;

    & * {
      color: inherit;
    }

    a {
      transition: 0.5s;
      &:hover {
        color: $color-sec;
      }
    }

    .lang {
      a {
        padding-left: 20px;
        margin-left: 20px;
        border-left: 1px solid #dddddd;

        i {
          margin-left: 8px;
          font-size: 16px;
        }
      }
    }

    .contact {
      a {
        &:not(:last-child) {
          margin-left: 30px;
        }
        font-size: 16px;
        font-family: "itMd";
      }

      i {
        margin-left: 13px;
      }
    }

    .social {
      margin-right: 40px;

      a {
        margin-left: 20px;
        font-size: 16px;

        &:last-child {
          margin-left: 0;
        }
      }
    }
  }
}
// End Up

// Start Navbar

.navbar {
  padding: 5px 0 50px;
  background-color: #fff;
  margin-bottom: 0;
  border-radius: 0;
  z-index: 9;
  transition: 0.5s;
  
  // &.scrolled {
  //   position: fixed;
  //   top: 0;
  //   padding: 10px 0 20px;
  //   box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  //   width: 100%;
  // }

  @include Sm {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    position: fixed;
    width: 100%;
    padding: 10px 0 20px;
  }

  .content {
    direction: ltr;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;

    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background-color: $color-sec;
      position: absolute;
      top: 88%;
      left: calc(100% + 35px);

      @include Sm {
        display: none;
      }
    }

    .logo{

      @include Sm{

        img{
          width: 250px;
        }
      }
      // .desk-logo{
        
      //   @include Sm{
      //     display: none
      //   }
      // }

      // .mob-logo{
      //   @include Md{
      //     display: none;
      //   }

      //   @include Sm{
      //     display: block;
      //   }

      //   img{
      //     width: 200px;
      //   }
      // }
    }

    .links {
      @include Sm {
        display: none;
      }

      ul {
        display: flex;
        align-items: center;
        direction: rtl;

        html[dir="ltr"] & {
            direction: ltr;
        }

        li {
          &:not(:last-child) {
            margin-left: 27px;

            html[dir="ltr"] & {
                margin-left: 0;
                margin-right: 27px;
            }
          }

          a {
            font-size: 17px;
            font-family: "itBold";
            color: #0c847b;
            transition: 0.5s;

            &:hover {
              color: $color-sec;
            }
          }
        }
      }
    }
  }
}

// End Navbar

// Video
.wrapper {
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 630px;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#0b837a, 0.1);
  }

  .wrapper__video {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    object-fit: cover;
    width: 100%;
    height: 630px;
  }

  .content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 200px;
    z-index: 2;

    @include Sm {
      right: 50%;
      transform: translateX(50%) translateY(-40%);
    }

    @include mob480 {
        width: 90%;
    }

    @include mob375 {
        width: 85%;
    }

    .text {
      width: 520px;
      height: 192px;
      padding: 35px 40px;
      background-color: #0b837a;
      z-index: 1;

        @include mob480 {
          width: 100%;
          height: 100%;
        }

        @include mob375 {
            padding: 15px;
        }

      h1 {
        color: #fff;
        font-size: 28px;
        font-family: "itBold";
        @include mob375 {
            font-size: 24px
        }
      }

      p {
        font-size: 17px;
        color: rgb(255, 255, 255);
        line-height: 1.412;
      }
    }

    &:after {
      content: "";
      display: block;
      width: calc(100% + 0px);
      height: calc(100% + 0px);
      border: 6px solid #fff;
      position: absolute;
      top: -20px;
      right: -20px;
      z-index: -1;
    }
  }
}
// Video

// Overview

.overview {
  padding: 120px 0;
  background-color: #fff;

  @include Sm {
    padding: 60px 0;
  }

  .Rtcontent {
    @include Sm {
      text-align: center;
    }

    h2 {
      font-size: 32px;
      color: #0c847b;
      font-family: "itBold";
      margin: 0;
      margin-bottom: 55px;
      position: relative;

      &:after {
        content: "";
        display: block;
        width: 115px;
        height: 2px;
        background-color: $color-sec;
        position: absolute;
        bottom: -20px;
        right: 0;

        @include Sm {
          right: 50%;
          transform: translateX(50%);
        }

        html[dir="ltr"] & {
          right: unset;
          left: 0;

          @include Sm {
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }
    }

    img {
      display: none;

      @include Sm {
        display: block;
        margin: auto;
        margin-bottom: 20px;
      }
    }

    p {
      color: rgb(95, 95, 95);
      font-size: 19px;
      line-height: 2;
      margin-bottom: 30px;

      @include Sm {
        margin-bottom: 10px;
      }
    }

    a {
      font-size: 16px;
      color: rgb(158, 158, 158);
      border-bottom: 1px solid rgb(158, 158, 158);
      transition: 0.5s;

      &:hover {
        color: $color-sec;
        border-bottom: 1px solid currentColor;
      }
    }
  }

  .pic {
    @include Sm {
      display: none;
    }
  }
}

// Overview

// Services

.services {
  background-color: #f9f9f9;
  padding: 120px 0 110px;
  background-image: url(../images/back.png);
  background-size: cover;

  @include Sm {
    padding: 60px 0 50px;

    background-position: 50%;
  }

  h2 {
    font-size: 28px;
    color: #0c847b;
    font-family: "itBold";
    margin: 0;
    margin-bottom: 25px;

    & + p {
      font-size: 19px;
      color: #5f5f5f;
      margin-bottom: 65px;
    }
  }

  .serv_container {
    display: flex;
    justify-content: space-between;

    @include Sm {
      flex-direction: column;
      align-items: center;
    }

    .service {
      @include Sm {
        margin-bottom: 20px;
      }

      img {
        margin: auto;
        margin-bottom: 25px;
        transition: 0.5s;
      }

      p {
        font-size: 20px;
        color: #0c847b;
        margin-bottom: 0;
        transition: 0.5s;
      }

      &:hover {
        img {
          transform: rotate(2deg) scale(1.06);
          animation: flash 0.5s linear;
        }

        p {
          color: $color-sec;
        }
      }
    }
  }
}

// Services

// Footer

footer {
  background-color: #0b837a;
  padding: 35px 0;

  @include Sm{
    padding: 15px 0;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    & > div {
      width: calc(100% / 3);
    }

    @include Xs {
      flex-direction: column;
      align-items: center;

      & > div {
        width: 100%;
      }
    }
  }

  .logo {
    @include Xs {
      order: 1;
      margin-bottom: 15px
    }

    img {
      filter: brightness(0) invert(1);

      @include Xs {
        margin: auto;
      }
    }

    @include Sm{
      img{
        height: 100px;
      }
    }
  }

  .copyright {
    text-align: center;
    font-size: 14px;
    color: #fff;

    @include Xs {
      order: 3;
    }
  }

  .social {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include Xs {
      order: 2;
      justify-content: center;
      margin-bottom: 15px
    }

    a {
      font-size: 20px;
      color: #fff;
      transition: 0.5s;

      &:not(:last-child) {
        margin-left: 27px;

        html[dir="ltr"] & {
            margin-left: 0;
            margin-right: 27px;
        }
      }

      &:hover {
        color: $color-sec;
      }
    }
  }
}

